import React from 'react';
import { Row, Col, Menu, Icon, Tabs, Form, Input, Button, CheckBox, message, Modal } from 'antd';
import { Link } from 'react-router-dom';

const SubMenu = Menu.SubMenu;
const MenuItemGroup = Menu.ItemGroup;
const FormItem = Form.Item;
const TabPane = Tabs.TabPane;

class PCHeader extends React.Component {
  constructor() {
    super();
    this.state = {
      current: 'top',
      modalVisable: false,
      action: 'login',
      hasLogined: false,
      userNickName: '',
      userid: 0
    };
    this.setModalVisible = this.setModalVisible.bind(this);
    this.handleClick = this.handleClick.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.changeAction = this.changeAction.bind(this);
    this.logout = this.logout.bind(this);
  }

  componentWillMount() {
    if (localStorage.userid !== '') {
      this.setState({
        hasLogined: true,
        userNickName: localStorage.userNickName,
        userid: localStorage.userid
      });
    }
  }

  setModalVisible(value) {
    this.setState({ modalVisable: value });
  }

  handleClick(e) {
    this.setState({ current: e.key })
    if (e.key === 'register') {
      this.setModalVisible(true);
    }
  }

  handleSubmit(e) {
    e.preventDefault();
    const myFetchOptions = {
      method: 'GET'
    }

    let formData = this.props.form.getFieldsValue();
    fetch('http://newsapi.gugujiankong.com/Handler.ashx?action=' + this.state.action + '&username='
      + formData.userName + '&password=' + formData.password + '&r_userName=' + formData.r_userName + '&r_password=' + formData.r_password + '&r_confirmPassword=' + formData.r_confirmPassword, myFetchOptions).then(response => response.json()).then(json => {
        this.setState({ userNickName: json.NickUserName, userid: json.UserId });
        localStorage.userid = json.UserId;
        localStorage.userNickName = json.NickUserName;
        if (this.state.action === 'login') {
          this.setState({ hasLogined: true });
        }
        message.success('请求成功');
      });
    this.setModalVisible(false);
  }

  changeAction(key) {
    if (key === '1') {
      this.setState({ action: 'login' });
    } else if (key === '2') {
      this.setState({ action: 'register' });
    }
  }

  logout() {
    localStorage.userid = '';
    localStorage.userNickName = '';
    this.setState({ hasLogined: false });
  }

  render() {
    const { getFieldDecorator } = this.props.form;
    const userShow = this.state.hasLogined
      ? <Menu.Item key="login" className="register">
        <Button type="primary" htmlType="button">{this.state.userNickName}</Button>
        <Link to="/usercenter" target="_blank">
          <Button type="dashed" htmlType="button">个人中心</Button>
        </Link>
        <Button type="ghost" htmlType="button" onClick={this.logout}>退出</Button>
      </Menu.Item>
      : <Menu.Item key="register" className="register">
        <Icon type="appstore" />注册/登录
        </Menu.Item>;

    return (
      <header>
        <Row>
          <Col span={2}></Col>
          <Col span={4}>
            <a herf="/" className="logo">
              <img src="/src/images/logo.png" alt="logo" />
              <span>News</span>
            </a>
          </Col>
          <Col span={16}>
            <Menu mode="horizontal" onClick={this.handleClick} defaultSelectedKeys={[this.state.current]}>
              <Menu.Item key="top"><Icon type="appstore" />头条</Menu.Item>
              <Menu.Item key="shehui"><Icon type="appstore" />社会</Menu.Item>
              <Menu.Item key="guonei"><Icon type="appstore" />国内</Menu.Item>
              <Menu.Item key="guoji"><Icon type="appstore" />国际</Menu.Item>
              <Menu.Item key="yule"><Icon type="appstore" />娱乐</Menu.Item>
              <Menu.Item key="tiyu"><Icon type="appstore" />体育</Menu.Item>
              <Menu.Item key="keji"><Icon type="appstore" />科技</Menu.Item>
              <Menu.Item key="shishang"><Icon type="appstore" />时尚</Menu.Item>
              {userShow}
            </Menu>

            <Modal title="用户中心" wrapClassName="vertical-center-modal"
              visible={this.state.modalVisable} onCancel={() => this.setModalVisible(false)}
              onOk={() => this.setModalVisible(false)} cancelText="取消" okText="关闭">
              <Tabs type="card" onChange={this.changeAction}>
                <TabPane tab="登录" key="1">
                  <Form onSubmit={this.handleSubmit}>
                    <FormItem label="账户">
                      {getFieldDecorator('userName')(
                        <Input placeholder="请输入您的账号" />
                      )}
                    </FormItem>
                    <FormItem label="密码">
                      {getFieldDecorator('password')(
                        <Input type="password" placeholder="请输入您的密码" />
                      )}
                    </FormItem>
                    <Button type="primary" htmlType="submit">登录</Button>
                  </Form>
                </TabPane>

                <TabPane tab="注册" key="2">
                  <Form onSubmit={this.handleSubmit}>
                    <FormItem label="账户">
                      {getFieldDecorator('r_userName')(
                        <Input placeholder="请输入您的账号" />
                      )}
                    </FormItem>
                    <FormItem label="密码">
                      {getFieldDecorator('r_password')(
                        <Input type="password" placeholder="请输入您的密码" />
                      )}
                    </FormItem>
                    <FormItem label="确认密码">
                      {getFieldDecorator('r_confirmPassword')(
                        <Input type="password" placeholder="请再次输入您的密码" />
                      )}
                    </FormItem>
                    <Button type="primary" htmlType="submit">注册</Button>
                  </Form>
                </TabPane>
              </Tabs>
            </Modal>
          </Col>
          <Col span={2}></Col>
        </Row>
      </header>
    );
  }
}

export default PCHeader = Form.create()(PCHeader);



